<template>
  <div id="app">
    <p>Andi DB</p> 
    <list v-bind:items="table_names" v-on:item-clicked="select_table" class="navbar"/>
    <mytable/>

  </div>
</template>

<script>
import list from './components/VueComponents/list.vue'
import mytable from './components/mytable.vue'

let socket;
export default {
  name: 'App',
  components: {
    list, mytable
  },
  methods:{
    select_table(table){
      socket.emit('get_table', {name:table.text})
    }
  },
  sockets:{
    connect: function () {
      socket = this.$socket
      this.$socket.emit('get_tables', 'hehe');
      
    },
    set_tables: function(data){
      this.table_names = []
      data.table_names.forEach((item,i) => {
        this.table_names.push({id:i, text:item, active:!i})
      });

    }
  },
  data(){
    return{
      table_names: []
    }
  }, created(){
  }
}
</script>

