<template>
  <div id="">
      <div ref="spreadsheet"></div>
  </div>
</template>

<script>
import jexcel from 'jexcel'
import 'jexcel/dist/jexcel.css'
import vue from 'vue'

var data = [
]
let currently_edited_row = -1;
var options = {
  data: data,
  allowToolbar:false,
  onbeforeinsertrow: () => false,
  onbeforedeleterow: () => false,

  onbeforeinsertcolumn: () => false,
  onbeforedeletecolumn: () => false,
  oneditionstart: (table, cell, col, row) => { 
    if(col == 4)
      currently_edited_row = row },
  oneditionend : (table, cell, col, row, value, newvalue) => {
    if(col == 4){
      socket.emit('set_value', {table: vue.$current_table, index: currently_edited_row, value:value});
      currently_edited_row = -1 
    }
    
  },
  onchange: (table, cell, col, row, value, newvalue) => { //is mabye called by maschine change
   if(col == 3)
      socket.emit('lock_value', {table: vue.$current_table, index: row, value:value});
  },
  columns: [
    { type: 'numeric', title: 'I', width: '50px', onlyread:true},

    { type: 'text', title: 'type', width: '250px' },
    { type: 'text', title: 'name', width: '500px' },
    { type: 'checkbox', title: 'locked', width: '250px' },
    { type: 'numeric', title: 'value', width: '350px' },
    { type: 'text', title: 'comment', width: '450px' },

  ]
}
let socket;
let spreadsheet ;
export default {
  name: 'mytable',
  components: {
    
  },
  sockets:{
    connect: function () {
      console.log('connectd')
      socket = this.$socket;
    },
    set_table: function(data){
      if(vue.$current_table != data.name){
        vue.$current_table = data.name
        let table = data.table.map((item, i) => [i, item.type, item.name, item.locked, item.value,  item.comment])
        if(spreadsheet)
          spreadsheet.setData(table)
      }else{
        let cells = spreadsheet.getColumnData(4)
        data.table.forEach((item, i) => {
          if(i == currently_edited_row)
            console.log('blocked', i);
          if(item.value != cells[i] && i != currently_edited_row)
            spreadsheet.setValueFromCoords(4, i, item.value)
        });
      }
      setTimeout(()=>{
        socket.emit('get_table', {name:vue.$current_table})
      }, 500)
    }
  },
  data(){
    return{}
  },
  mounted: function () {
    spreadsheet = jexcel(this.$el, options)
    spreadsheet.hideIndex()
    Object.assign(this, { spreadsheet })
  }
}
</script>

<style>
td{
  font-size: 20pt;
}
input[type=checkbox] {
    transform: scale(2);
}
</style>
